import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { bindActionCreators } from 'redux';
import {
  Menu,
  Spin,
  Row,
  Col,
  Tag,
  Tooltip,
  Icon,
  Popover,
  Dropdown
} from 'antd';
import moment from 'moment';
let QRCode;

import DocumentTitle from '../../component/document-title';
import github from '../../lib/github';
import { firstUpperCase } from '../../lib/utils';
import * as postAction from '../../redux/post';
import pkg from '../../../package.json';
import Comments from '../../component/comments';

import './post.css';

class Post extends Component {
  state = {};

  async componentWillMount() {
    let { number } = this.props.match.params;
    require.ensure(
      [],
      require => {
        QRCode = require('qrcode.react');
      },
      'react-qrcode'
    );
    if (number) {
      await this.getPost(number);
    }
  }

  async componentWillReceiveProps(nextProp) {
    const { number } = nextProp.match.params;
    if (number && number !== this.props.match.params.number) {
      await this.getPost(nextProp.match.params.number);
    }
  }

  async getPost(number) {
    let post = {};
    try {
      const {
        data
      } = await github.get(
        `/repos/${pkg.config.owner}/${pkg.config.repo}/issues/${number}`,
        {
          headers: {
            Accept: 'application/vnd.github.v3.html'
          },
          responseType: 'text'
        }
      );
      post = data;
    } catch (err) {
      console.error(err);
    }
    this.props.setPost({ [number]: post });
    return post;
  }

  getShareMenu(post) {
    const shareMenu = [
      {
        title: '',
        url: `http://service.weibo.com/share/share.php?appkey=&title=${': ' + post.title}&url=${location.href}&pic=&searchPic=false&style=simple`
      },
      {
        title: ' Twitter',
        url: `https://twitter.com/intent/tweet?text=${': ' + post.title}&url=${location.href}&via=Axetroy`
      },
      {
        title: ' Telegram',
        url: `https://telegram.me/share/url?url=${location.href}&text=${': ' + post.title}`
      },
      {
        title: ' QQ',
        url: `http://connect.qq.com/widget/shareqq/index.html?site=Axetroy's NeverLand&title=${': ' + post.title}&summary= Axetroy's NeverLand&pics=&url=${location.href}`
      }
    ];
    return (
      <Menu>
        {shareMenu.map(menu => {
          return (
            <Menu.Item key={menu.title}>
              <a target="_blank" href={menu.url}>
                {menu.title}
              </a>
            </Menu.Item>
          );
        })}
      </Menu>
    );
  }
  render() {
    const { number } = this.props.match.params;
    const post = this.props.POST[number] || {};
    return (
      <DocumentTitle title={post.title} suffix={['']}>
        <Spin spinning={!Object.keys(post).length}>
          <Row>
            <Col
              style={{
                marginBottom: '2rem',
                paddingBottom: '2rem',
                borderBottom: '1px solid #e6e6e6'
              }}
            >
              {post.user && post.user.avatar_url
                ? <img
                    src={post.user.avatar_url}
                    alt=""
                    style={{
                      width: '4.4rem',
                      height: '100%',
                      borderRadius: '50%',
                      verticalAlign: 'middle'
                    }}
                  />
                : ''}
              <div
                style={{
                  display: 'inline-block',
                  verticalAlign: 'middle',
                  margin: '0 1rem'
                }}
              >
                <strong>
                  <Icon
                    type="user"
                    style={{
                      marginRight: '0.5rem'
                    }}
                  />{firstUpperCase(post && post.user ? post.user.login : '')}
                </strong>
                <p>
                  <Icon type="calendar" style={{ marginRight: '0.5rem' }} />
                  {moment(new Date(post.created_at)).fromNow()}
                </p>
                <p>
                  <Icon
                    type="message"
                    style={{
                      marginRight: '0.5rem'
                    }}
                  />
                  {post.comments}
                </p>
              </div>
              <div
                style={{
                  textAlign: 'right',
                  float: 'right',
                  fontSize: '2.4rem'
                }}
              >
                <span style={{ margin: '0.5rem' }}>
                  <Tooltip title="" placement="topRight">
                    <a
                      target="blank"
                      href={`https://github.com/${pkg.config.owner}/${pkg.config.repo}/issues/${post.number}`}
                      style={{
                        color: 'inherit'
                      }}
                    >
                      <Icon type="edit" />
                    </a>
                  </Tooltip>
                </span>
                <span
                  style={{
                    cursor: 'pointer',
                    margin: '0.5rem'
                  }}
                >
                  <Popover
                    placement="topLeft"
                    title={''}
                    trigger="click"
                    content={
                      <div className="text-center">
                        {QRCode
                          ? <QRCode value={location.href} />
                          : 'Loading...'}
                      </div>
                    }
                  >
                    <Icon type="qrcode" />
                  </Popover>
                </span>
                <span
                  style={{
                    cursor: 'pointer',
                    margin: '0.5rem'
                  }}
                >
                  <Dropdown
                    overlay={this.getShareMenu(post)}
                    trigger={['click']}
                  >
                    <Icon type="share-alt" />
                  </Dropdown>
                </span>
              </div>
            </Col>

            <h3
              style={{
                textAlign: 'center'
              }}
            >
              {post.title} <span
                style={{
                  verticalAlign: 'top'
                }}
              >
                {(post.labels || []).map(label => {
                  return (
                    <Tag key={label.id} color={'#' + label.color}>
                      {label.name}
                    </Tag>
                  );
                })}
              </span>
            </h3>

            <div
              className="markdown-body post-content"
              style={{
                margin: '2rem 0',
                borderBottom: '1px solid #e6e6e6',
                paddingBottom: '2rem'
              }}
              dangerouslySetInnerHTML={{
                __html: post.body_html
              }}
            />

            <blockquote>
              <p></p>
              <p>1. , .</p>
              <p>2. , .</p>
              <p>3. .</p>
            </blockquote>

            <div
              style={{
                marginTop: '2rem',
                paddingTop: '2rem',
                borderTop: '1px solid #e6e6e6'
              }}
            >
              <Comments
                type="issues"
                owner={pkg.config.owner}
                repo={pkg.config.repo}
                number={post.number}
              />
            </div>
          </Row>
        </Spin>
      </DocumentTitle>
    );
  }
}
export default connect(
  function mapStateToProps(state) {
    return { POST: state.POST };
  },
  function mapDispatchToProps(dispatch) {
    return bindActionCreators({ setPost: postAction.set }, dispatch);
  }
)(withRouter(Post));



