import React, { Component, PropTypes } from 'react';
import { lazyload } from 'react-lazyload';
import moment from 'moment';

import github from '../../lib/github';

@lazyload({
  height: 200,
  offset: 100,
  once: true
})
class Comments extends Component {
  static propTypes = {
    type: PropTypes.string.isRequired,
    owner: PropTypes.string,
    repo: PropTypes.string,
    number: PropTypes.number,
    gistId: PropTypes.string
  };
  state = {
    comments: []
  };

  async componentWillMount() {
    const { type, owner, repo, number, gistId } = this.props;
    if (type === 'gist') {
      this.getGistComments(gistId);
    } else if (type === 'issues') {
      this.getIssuesComments(owner, repo, number);
    }
  }

  async getIssuesComments(owner, repo, number) {
    let comments = [];
    try {
      const {
        data
      } = await github.get(
        `/repos/${owner}/${repo}/issues/${number}/comments`,
        {
          headers: {
            Accept: 'application/vnd.github.v3.html'
          },
          responseType: 'text'
        }
      );
      this.setState({ comments: data });
    } catch (err) {
      console.error(err);
    }
    return comments;
  }

  async getGistComments(gist_id) {
    let comments = [];
    try {
      const { data } = await github.get(`/gists/${gist_id}/comments`, {
        headers: {
          Accept: 'application/vnd.github.v3.html'
        },
        responseType: 'text'
      });
      comments = comments.concat(data || []);

      this.setState({ comments });
    } catch (err) {
      console.error(err);
    }
    return comments;
  }

  render() {
    const { type, owner, repo, number, gistId } = this.props;
    return (
      <div>
        <h3>
          :
          <a
            target="_blank"
            href={
              type === 'issues'
                ? `https://github.com/${owner}/${repo}/issues/${number}`
                : type === 'gist'
                    ? `https://gist.github.com/${gistId}`
                    : 'javascript:void 0'
            }
            style={{
              float: 'right'
            }}
          >
            
          </a>
        </h3>

        {this.state.comments.length
          ? this.state.comments.map(comment => {
              return (
                <div
                  key={comment.id}
                  style={{
                    border: '0.1rem solid #e2e2e2',
                    borderRadius: '0.5rem',
                    margin: '1rem 0'
                  }}
                >
                  <div
                    className="comment-header"
                    style={{
                      overflow: 'hidden'
                    }}
                  >
                    <img
                      style={{
                        width: '3.2rem',
                        verticalAlign: 'middle',
                        borderRadius: '50%'
                      }}
                      src={comment.user.avatar_url}
                      alt=""
                    />
                    &nbsp;&nbsp;
                    <strong
                      style={{
                        color: '#586069'
                      }}
                    >
                      <a
                        target="_blank"
                        href={`https://github.com/${comment.user.login}`}
                      >
                        {comment.user.login}
                      </a>
                    </strong>
                    &nbsp;&nbsp;
                    <span>
                      {' '}
                      {`commented at ${moment(comment.created_at).fromNow()}`}
                      &nbsp;&nbsp;
                      {`updated at ${moment(comment.updated_at).fromNow()}`}
                    </span>
                  </div>
                  <div
                    className="comment-body"
                    style={{
                      padding: '1.2rem'
                    }}
                  >
                    <div
                      className="markdown-body"
                      dangerouslySetInnerHTML={{
                        __html: comment.body_html
                      }}
                    />
                  </div>
                </div>
              );
            })
          : <div>
              <p>!</p>
            </div>}
      </div>
    );
  }
}
export default Comments;



